import React, { useEffect, useState } from 'react';
import { SocialIcon } from 'react-social-icons';
import endpoints from '../constants/endpoints';

const styles = {
  iconStyle: {
    marginLeft: 10,
    marginRight: 10,
    marginBottom: 10,
  },
};

function Social() {
  const [data, setData] = useState(null);

  useEffect(() => {
    fetch(endpoints.social, {
      method: 'GET',
    })
      .then((res) => res.json())
      .then((res) => setData(res))
      .catch((err) => err);
  }, []);

  const customSites = [
    {
      network: 'hackthebox',
      href: 'https://app.hackthebox.com/users/348120',
      iconUrl: 'https://encrypted-tbn0.gstatic.com/images?q=tbn:ANd9GcSUAYaFuZb0d9f3OLGAoUAbeyrFHiWNTtD5LrYmXfDzow&s',
      style: {
        marginLeft: 10,
        marginRight: 10,
        marginBottom: 10,
        width: '60px',
        borderRadius: '50%',
      },
    },
    {
      network: 'tryhackme',
      href: 'https://tryhackme.com/p/Zeeshan1234',
      iconUrl: 'data:image/jpeg;base64,',
      style: {
        marginLeft: 10,
        marginRight: 10,
        marginBottom: 10,
        width: '60px',
        borderRadius: '50%',
      },
    },
    {
      network: 'pentesterlab',
      href: 'https://pentesterlab.com/profile/Zeeshan1234',
      iconUrl: 'https://encrypted-tbn0.gstatic.com/images?q=tbn:ANd9GcQE_rab7QbwdKkFEPCC9YT9_FVUHEccfnlrJw6nw366dw&s',
      style: {
        marginLeft: 10,
        marginRight: 10,
        marginBottom: 10,
        width: '60px',
        borderRadius: '50%',
      },
    },
    {
      network: 'pwn.college',
      href: 'https://pwn.college/p/4912',
      iconUrl: 'https://avatars.githubusercontent.com/u/42601809?s=500',
      style: {
        marginLeft: 10,
        marginRight: 10,
        marginBottom: 10,
        width: '60px',
        borderRadius: '50%',
      },
    },
  ];

  return (
    <div className="social">
      {data ? data.social.map((social) => (
        <SocialIcon
          key={social.network}
          style={styles.iconStyle}
          url={social.href}
          network={social.network}
          // bgColor={theme.socialIconBgColor}
          target="_blank"
          rel="noopener"
        />
      )) : null}

      {customSites.map((site) => (
        <a
          key={site.network}
          href={site.href}
          target="_blank"
          rel="noreferrer"
        >
          <img
            src={site.iconUrl}
            alt={site.network}
            style={site.style}
          />
        </a>
      ))}
    </div>
  );
}

export default Social;
